"use client"

import axios from "axios";

const page = () => {
  const {register}

  const handleSubmit = async () => {
    try {
      const res = await axios.post("http://localhost:5200/api/restaurants/create-place", {});

    } catch (err) {
      console.log(err);
    }
  }

  return (
    <div className='border-borderColor border-[1px] rounded-lg p-6 flex flex-col  '>
      <div className="mb-2  ">
        <h1 className='leadiing-7 pb-1  text-2xl font-semibold border-b-[1px] border-borderColor inline-block'>Add a new restaurant</h1>
      </div>
      <form onSubmit={handleSubmit}>
        <div className='grid grid-cols-2 mb-4'>
          <div className="p-2 flex flex-col gap-3">
            <div className="flex flex-col gap-2">
              <label className='text-lg font-medium leading-[22px]' htmlFor="#">Title</label>
              <input placeholder='Type in title' type="text" className='input p-1' />
            </div>
            <div className="flex flex-col gap-2">
              <label className='text-lg font-medium leading-[22px]' htmlFor="#">Description</label>
              <textarea placeholder='Description' className='resize-none input p-1 h-[100px]'></textarea>
            </div>
            <div className="flex flex-col gap-2">
              <label className='text-lg font-medium leading-[22px]' htmlFor="#">Adress</label>
              <input placeholder='Type in adress' type="text" className='input p-1' />
            </div>
          </div>
          <div className="p-2 flex flex-col gap-3">
            <div className="flex flex-col gap-2">
              <label className='text-lg font-medium leading-[22px]' htmlFor="#">Contacts</label>
              <input placeholder='Type in phone number' type="tel" className='input p-1' />
              <input placeholder='Type in website url' type="tel" className='input p-1' />
            </div>
            <div className="flex flex-col gap-2">
              <label className='text-lg font-medium leading-[22px]' htmlFor="#">Image</label>
              <input type="file" className='input p-1  file:flex ' />
            </div>
          </div>
        </div>

        <button type="submit" className="btn ml-2 p-3 text-lg w-[100px]">Create</button>
      </form>
    </div>
  )
}

export default page